import React from 'react'
import IBaseProps from '../../base/baseProps'
import BaseComponent from '../../base/baseComponent'
import Dialog from '../dialog'

export interface IJumpPromptProps extends IBaseProps {
  /**
   * 
   */
  onConfirm?: () => void
  /**
   * history.goBack()
   */
  onCancel?: (e: any) => void
  /**
   * true
   */
  when?: boolean
  /**
   * Dialog, 
   */
  dialogChildren?: JSX.Element | string
  /**
   * Dialogtitle, 
   */
  dialogTitle?: string
  /**
   * dialog
   */
  okText?: string
  /**
   * dialog
   */
  cancelText?: string
  /**
   * history
   */
  history: any
}

export default class JumpPrompt extends BaseComponent<IJumpPromptProps, any> {
  static defaultProps = {
    onConfirm: null,
    onCancel: null,
    when: true,
    dialogChildren: [
      <div key='dialogChildren1'></div>,
      <div key='dialogChildren2'></div>,
    ],
    okText: '',
    cancelText: '',
    dialogTitle: '',
    history: null,
  }

  state = {
    isShowModal: false,
    nextLocation: null as any,
  }

  unblock: any

  // pathname
  currentPathname: string = window.location.pathname

  // search
  currentSearch: string = window.location.search

  componentDidMount() {
    const { history } = this.props
    if (!history) {
      return console.warn('history')
    }
    // history.blockblock
    this.unblock = history.block((nextLocation, action) => {
      // NOTE safarisafariwindow.location.hrefA -> B, B
      // ApopStateblock
      // location.keynextLocation.keykey
      const { location } = history
      if (location.key === nextLocation.key) {
        return
      }
      // whentruehistorypop
      // TODO 
      if (this.props.when && action.toLowerCase() === 'pop') {
        // NOTE 
        setTimeout(() => {
          if (window.location.pathname !== this.currentPathname) {
            history.push(`${this.currentPathname}${this.currentSearch}`)
          }
        }, 200)
        this.setState({
          isShowModal: true,
          nextLocation,
        })
        return false
      }
    })
  }

  componentWillUnmount() {
    this.unblock()
  }

  onCancel = () => {
    this.unblock()
    const { nextLocation } = this.state
    const {
      onCancel,
      history = {
        goBack: () => {
          console.warn('history ')
        },
      },
    } = this.props
    if (onCancel) {
      onCancel(nextLocation)
    } else {
      history.goBack()
    }
  }

  onConfirm = () => {
    this.setState({ nextLocation: null, isShowModal: false })
    const { onConfirm } = this.props
    if (onConfirm) {
      onConfirm()
    }
  }

  render() {
    const { isShowModal } = this.state
    const { dialogChildren, dialogTitle, okText, cancelText } = this.props

    return (
      <div className={`${this.classPrefix}jump-prompt`}>
        <Dialog
          title={dialogTitle}
          visible={isShowModal}
          onOk={this.onConfirm}
          onCancel={this.onCancel}
          mask
          okText={okText}
          cancelText={cancelText}
        >
          {dialogChildren}
        </Dialog>
      </div>
    )
  }
}
