import React from 'react'
import classNames from 'classnames'
import IBaseProps from '../../base/baseProps'
import Popup from '../popup'
import Icon from '../icon'
import BaseComponent from '../../base/baseComponent'
// import '@q/q-ui/dist/scss/components/dialog/index.scss'

/**
 * url: 
 * theme:  icon- image- bigImage-
 */
interface Image {
  url?: string
  theme?: string // 'icon' | 'image' | 'bigImage'
}

export interface IDialogProps extends IBaseProps {
  visible: boolean
  hasCloseButton?: boolean
  footer?: string | JSX.Element
  hasShowCancel?: boolean
  title?: string | JSX.Element
  mask?: boolean
  maskClosable?: boolean
  okText?: string
  cancelText?: string
  image?: Image
  animation?: string // 'slide' | 'zoom' | 'drop' | false;
  onCancel?: (val?: any) => void
  onOk?: (val?: any) => void
}
/**
 * Dialog
 * @prop {string} className dialog
 * @prop {object} style dialog
 * @prop {boolean} visible 
 * @prop {boolean} footer  (JSX.Element or nofooter)
 * @prop {boolean} hasCloseButton 
 * @prop {boolean} hasShowCancel 
 * @prop {string} title 
 * @prop {boolean} mask 
 * @prop {boolean} maskClosable 
 * @prop {string} okText 
 * @prop {string} cancelText 
 * @prop {Image} image  url; theme,icon- image- bigImage-;
 * @prop {string} animation 
 * @prop {function} onCancel 
 * @prop {function} onOk 
 * @example
 */

export default class Dialog extends BaseComponent<IDialogProps, any> {
  static defaultProps = {
    hasCloseButton: false,
    footer: false,
    hasShowCancel: true,
    mask: true,
    maskClosable: false,
    okText: '',
    cancelText: '',
    image: {
      url: '',
      theme: 'image'
    },
    animation: 'zoom',
    onOk() {},
    onCancel() {}
  }
  onCancel = () => {
    this.props.onCancel()
  }
  onOK = () => {
    this.props.onOk()
  }

  render() {
    const {
      className,
      style,
      mask,
      maskClosable,
      visible,
      hasCloseButton,
      okText,
      cancelText,
      image,
      children,
      footer,
      hasShowCancel,
      title,
      animation
    } = this.props
    const {url = '', theme= 'image' } = image
    return (
      <Popup
        onClose={this.onCancel}
        visible={visible}
        mask={mask}
        maskClosable={maskClosable}
        position="center"
        animation={animation}
      >
        <div
          className={classNames(`${this.classPrefix}dialog`, className)}
          style={style}
        >
          <div className={`${this.classPrefix}dialog__hd`}>
            {url && (
              <div
                className={classNames(
                  `${this.classPrefix}dialog__hd__image`,
                  `${this.classPrefix}dialog__hd__image--${theme}`
                )}
              >
                <img src={url} />
              </div>
            )}
            {hasCloseButton && (
              <div
                className={classNames({
                  [`${this.classPrefix}dialog__hd__close`]: true,
                  [`${this.classPrefix}dialog__hd__close--primary`]:
                  theme === 'bigImage'
                })}
                onClick={this.onCancel}
              >
                <Icon type="closePop" style={{ fontSize: 18 }} />
              </div>
            )}
            {title && (
              <div
                className={classNames({
                  [`${this.classPrefix}dialog__hd__title`]: true,
                  [`${this.classPrefix}dialog__hd__title--nocontent`]: !children
                })}
              >
                {title}
              </div>
            )}
          </div>
          {children && (
            <div className={`${this.classPrefix}dialog__bd`}>
              <div
                className={classNames({
                  [`${this.classPrefix}dialog__bd__content`]: true,
                  [`${this.classPrefix}dialog__bd__content--notitle`]: !title
                })}
              >
                {children}
              </div>
            </div>
          )}
          {!footer && (
            <div className={`${this.classPrefix}dialog__ft`}>
              {hasShowCancel && (
                <a
                  className={`${this.classPrefix}dialog__btn ${
                    this.classPrefix
                  }dialog__btn--default`}
                  onClick={this.onCancel}
                >
                  {cancelText}
                </a>
              )}
              <a
                className={`${this.classPrefix}dialog__btn ${
                  this.classPrefix
                }dialog__btn--primary`}
                onClick={this.onOK}
              >
                {okText}
              </a>
            </div>
          )}
          {footer &&
            footer !== 'nofooter' && (
            <div className={`${this.classPrefix}dialog__ft`}>{footer}</div>
          )}
        </div>
      </Popup>
    )
  }
}
